// src/components/workout/AllFatigueInsights.tsx
import React, { useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { 
  calculateMuscleFatigue, 
  calculateRecoveryProfiles, 
  generateFatigueInsights,
  FatigueInsight
} from '@/utils/muscleFatigueCalculator';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { AlertCircle, CheckCircle, Info } from 'lucide-react';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { useFatigueSettings } from '@/contexts/FatigueSettingsContext';

interface AllFatigueInsightsProps {
  logs: WorkoutLogEntry[];
}

export const AllFatigueInsights: React.FC<AllFatigueInsightsProps> = ({ logs }) => {
  const { settings } = useFatigueSettings();
  
  // Generate all insights from workout logs
  const { allInsights, warningInsights, positiveInsights, infoInsights } = useMemo(() => {
    if (!logs || logs.length === 0) {
      return { 
        allInsights: [], 
        warningInsights: [], 
        positiveInsights: [], 
        infoInsights: [] 
      };
    }
    
    const fatigueData = calculateMuscleFatigue(logs, undefined, settings);
    const recoveryProfiles = calculateRecoveryProfiles(logs, settings);
    const insights = generateFatigueInsights(fatigueData, recoveryProfiles);
      return {
      allInsights: insights,
      warningInsights: insights.filter(insight => insight.type === 'warning'),
      positiveInsights: insights.filter(insight => insight.type === 'positive'),
      infoInsights: insights.filter(insight => insight.type === 'info')
    };
  }, [logs, settings]);
  
  if (!logs || logs.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="text-lg font-semibold">Recovery Insights</CardTitle>
          <CardDescription>
            Log workouts to receive personalized recovery insights
          </CardDescription>
        </CardHeader>
        <CardContent className="flex items-center justify-center h-60">
          <p className="text-muted-foreground">No workout data available</p>
        </CardContent>
      </Card>
    );
  }
  
  if (allInsights.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="text-lg font-semibold">Recovery Insights</CardTitle>
          <CardDescription>
            Log more workouts to generate personalized insights
          </CardDescription>
        </CardHeader>
        <CardContent className="flex items-center justify-center h-60">
          <p className="text-muted-foreground">Not enough data for insights yet</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg font-semibold">Recovery Insights</CardTitle>
        <CardDescription>
          Personalized insights to optimize your training and recovery cycle
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="all">
          <TabsList className="grid grid-cols-4 mb-4">
            <TabsTrigger value="all" className="relative">
              All
            </TabsTrigger>
            <TabsTrigger value="warnings" className="relative">
              Warnings
            </TabsTrigger>
            <TabsTrigger value="positive" className="relative">
              Positive
            </TabsTrigger>
            <TabsTrigger value="tips" className="relative">
              Tips
            </TabsTrigger>
          </TabsList>
          
          <TabsContent value="all" className="space-y-4">
            {allInsights.length === 0 ? (
              <p className="text-center text-muted-foreground py-8">No insights available</p>
            ) : (
              <InsightsList insights={allInsights} />
            )}
          </TabsContent>
          
          <TabsContent value="warnings" className="space-y-4">
            {warningInsights.length === 0 ? (
              <p className="text-center text-muted-foreground py-8">No warnings - your training seems well balanced!</p>
            ) : (
              <InsightsList insights={warningInsights} />
            )}
          </TabsContent>
          
          <TabsContent value="positive" className="space-y-4">
            {positiveInsights.length === 0 ? (
              <p className="text-center text-muted-foreground py-8">No positive insights yet - keep training!</p>
            ) : (
              <InsightsList insights={positiveInsights} />
            )}
          </TabsContent>
          
          <TabsContent value="tips" className="space-y-4">
            {infoInsights.length === 0 ? (
              <p className="text-center text-muted-foreground py-8">No training tips available yet</p>
            ) : (
              <InsightsList insights={infoInsights} />
            )}
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};

// Helper component to display a list of insights
const InsightsList: React.FC<{ insights: FatigueInsight[] }> = ({ insights }) => {
  return (
    <div className="space-y-4">
      {insights.map((insight, idx) => (
        <div 
          key={idx} 
          className={`p-4 rounded-lg border
            ${insight.type === 'warning' ? 'bg-red-500/5 border-red-500/20' : 
              insight.type === 'positive' ? 'bg-green-500/5 border-green-500/20' : 
              'bg-blue-500/5 border-blue-500/20'}`}
        >
          <div className="flex gap-3">
            <div className="mt-1">
              {insight.type === 'warning' ? (
                <AlertCircle className="h-5 w-5 text-red-500" />
              ) : insight.type === 'positive' ? (
                <CheckCircle className="h-5 w-5 text-green-500" />
              ) : (
                <Info className="h-5 w-5 text-blue-500" />
              )}
            </div>
            
            <div>
              <h4 className="text-base font-semibold mb-1">{insight.muscleGroup}</h4>
              <p className="text-sm mb-2">{insight.insight}</p>
              <p className={`text-sm font-medium
                ${insight.type === 'warning' ? 'text-red-500' : 
                  insight.type === 'positive' ? 'text-green-500' : 
                  'text-blue-500'}`}>
                {insight.suggestion}
              </p>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};
