// src/components/workout/WorkoutRecommender.tsx
import React, { useMemo, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { calculateMuscleFatigue, getMusclesReadyForTraining } from '@/utils/muscleFatigueCalculator';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { useExercises, useWorkoutTemplates } from '@/hooks/useConsolidatedApp';
import { useFatigueSettings } from '@/contexts/FatigueSettingsContext';
import { Lightbulb, Save, PlayCircle } from 'lucide-react';
import { toast } from 'sonner';
import { v4 as uuidv4 } from 'uuid';

interface WorkoutRecommenderProps {
  logs: WorkoutLogEntry[];
}

export const WorkoutRecommender: React.FC<WorkoutRecommenderProps> = ({ logs }) => {
  const { exercises } = useExercises();
  const { saveTemplate, startWorkout } = useWorkoutTemplates();
  const { settings } = useFatigueSettings();
  
  const [workoutType, setWorkoutType] = useState<'fullBody' | 'split' | 'push' | 'pull' | 'legs'>('fullBody');
  const [duration, setDuration] = useState<'short' | 'medium' | 'long'>('medium');
  const [isGenerating, setIsGenerating] = useState(false);  const [recommendedWorkout, setRecommendedWorkout] = useState<{
    name: string;
    description: string;
    exercises: Array<{
      name: string;
      muscleGroup: string;
      sets: number;
      reps: number;
      weight: number;
    }>;
  } | null>(null);
  // Calculate which muscles are ready for training
  const readyMuscles = useMemo(() => {
    const fatigueData = calculateMuscleFatigue(logs, undefined, settings);
    return getMusclesReadyForTraining(fatigueData);
  }, [logs, settings]);

  const getExercisesByMuscleGroups = (muscleGroups: string[], count: number) => {
    // Filter exercises by the provided muscle groups
    const filteredExercises = exercises.filter(ex => 
      muscleGroups.some(m => ex.muscleGroup.toLowerCase() === m.toLowerCase())
    );
    
    // Use a map to ensure we don't get duplicate exercises
    const selectedExercises = new Map();
    
    // Try to get at least one exercise per muscle group first
    muscleGroups.forEach(muscleGroup => {
      const muscleExercises = filteredExercises.filter(ex => 
        ex.muscleGroup.toLowerCase() === muscleGroup.toLowerCase()
      );
      
      if (muscleExercises.length > 0) {
        // Get a random exercise for this muscle group
        const randomIndex = Math.floor(Math.random() * muscleExercises.length);
        const exercise = muscleExercises[randomIndex];
        selectedExercises.set(exercise.exercise, {
          name: exercise.exercise,
          muscleGroup: exercise.muscleGroup,
          sets: getRecommendedSets(duration),
          reps: getRecommendedReps(exercise.targetIntensity),
          weight: 0 // This will be filled in by the user when they start the workout
        });
      }
    });
    
    // If we haven't reached the desired count, add more exercises randomly
    while (selectedExercises.size < count && filteredExercises.length > selectedExercises.size) {
      const remainingExercises = filteredExercises.filter(ex => 
        !selectedExercises.has(ex.exercise)
      );
      
      if (remainingExercises.length === 0) break;
      
      const randomIndex = Math.floor(Math.random() * remainingExercises.length);
      const exercise = remainingExercises[randomIndex];
      
      selectedExercises.set(exercise.exercise, {
        name: exercise.exercise,
        muscleGroup: exercise.muscleGroup,
        sets: getRecommendedSets(duration),
        reps: getRecommendedReps(exercise.targetIntensity),
        weight: 0
      });
    }
    
    return Array.from(selectedExercises.values());
  };
  
  const getRecommendedSets = (duration: string) => {
    switch (duration) {
      case 'short': return 2;
      case 'long': return 4;
      default: return 3;
    }
  };
  
  const getRecommendedReps = (intensity: string | number) => {
    // Convert intensity to number if it's a string
    const intensityValue = typeof intensity === 'string' 
      ? parseInt(intensity) || 5 
      : intensity;
    
    // Adjust reps based on intensity
    if (intensityValue >= 8) return 6; // High intensity = lower reps
    if (intensityValue <= 3) return 15; // Low intensity = higher reps
    return 10; // Medium intensity = medium reps
  };

  const generateWorkout = () => {
    setIsGenerating(true);
    
    // If no muscles are ready, show a message
    if (readyMuscles.length === 0) {
      toast.error("No muscles ready for training", {
        description: "All your muscle groups need more recovery time."
      });
      setIsGenerating(false);
      return;
    }
    
    let targetMuscles: string[] = [];
    let exerciseCount = 0;
    let workoutName = '';
    let workoutDescription = '';
    
    // Determine which muscles to target based on workout type
    switch (workoutType) {
      case 'fullBody':
        targetMuscles = readyMuscles;
        exerciseCount = duration === 'short' ? 4 : duration === 'medium' ? 6 : 8;
        workoutName = 'Full Body Workout';
        workoutDescription = 'A complete workout targeting all the ready muscle groups';
        break;
        
      case 'push':
        // Filter push muscles
        targetMuscles = readyMuscles.filter(muscle => 
          ['chest', 'shoulders', 'triceps'].includes(muscle.toLowerCase())
        );
        exerciseCount = duration === 'short' ? 3 : duration === 'medium' ? 5 : 7;
        workoutName = 'Push Workout';
        workoutDescription = 'Chest, shoulders, and triceps focused workout';
        break;
        
      case 'pull':
        // Filter pull muscles
        targetMuscles = readyMuscles.filter(muscle => 
          ['back', 'biceps', 'forearms'].includes(muscle.toLowerCase())
        );
        exerciseCount = duration === 'short' ? 3 : duration === 'medium' ? 5 : 7;
        workoutName = 'Pull Workout';
        workoutDescription = 'Back and biceps focused workout';
        break;
        
      case 'legs':
        // Filter leg muscles
        targetMuscles = readyMuscles.filter(muscle => 
          ['quadriceps', 'hamstrings', 'calves', 'glutes'].includes(muscle.toLowerCase())
        );
        exerciseCount = duration === 'short' ? 3 : duration === 'medium' ? 5 : 7;
        workoutName = 'Leg Workout';
        workoutDescription = 'Lower body focused workout';
        break;
        
      case 'split':
        // Choose the muscle groups with the lowest fatigue
        targetMuscles = readyMuscles.slice(0, 2);
        exerciseCount = duration === 'short' ? 4 : duration === 'medium' ? 6 : 8;
        workoutName = `${targetMuscles.join(' & ')} Workout`;
        workoutDescription = `Focus workout targeting ${targetMuscles.join(' and ')}`;
        break;
    }
    
    // If no matching muscle groups are ready for the workout type
    if (targetMuscles.length === 0) {
      toast.error(`No muscles ready for ${workoutType} workout`, {
        description: "Try a different workout type or wait for recovery."
      });
      setIsGenerating(false);
      return;
    }
    
    // Generate the exercises for the workout
    const workoutExercises = getExercisesByMuscleGroups(targetMuscles, exerciseCount);
    
    // Create the recommended workout
    setRecommendedWorkout({
      name: workoutName,
      description: workoutDescription,
      exercises: workoutExercises
    });
    
    setIsGenerating(false);
    
    toast.success("Workout Plan Generated", {
      description: `Created a ${duration} ${workoutType} workout with ${workoutExercises.length} exercises`
    });
  };
  const handleSaveTemplate = () => {
    if (!recommendedWorkout) return;
      // Create a new template with the recommended workout
    saveTemplate({
      id: uuidv4(),
      name: recommendedWorkout.name,
      description: recommendedWorkout.description,
      exercises: recommendedWorkout.exercises.map(ex => ({
        ...ex,
        // Add the required fields for WorkoutExercise type
        rest: 60, // Default rest time in seconds
        notes: '',
        difficultyLevel: 'medium',
        equipmentRequired: '',
        targetIntensity: '5',
        primaryMuscleGroup: ex.muscleGroup,
        secondaryMuscleGroup: '',
        exerciseDuration: '',
        recoveryTime: '',
        exerciseType: 'strength',
        caloriesBurned: '',
        exerciseProgression: '',
        injuryRiskLevel: 'low',
        exerciseLink: '',
        imageLink: '',
        relativePath: ''      })),
      createdAt: new Date().toISOString(),
      lastUsed: ''
    });
    
    toast.success("Workout Template Saved", {
      description: "You can find it in your workout templates"
    });
  };
    const handleStartWorkout = () => {
    if (!recommendedWorkout) return;
    
    // First save it as a template
    const templateName = recommendedWorkout.name;
      saveTemplate({
      id: uuidv4(),
      name: templateName,
      description: recommendedWorkout.description,
      exercises: recommendedWorkout.exercises.map(ex => ({
        ...ex,
        // Add the required fields for WorkoutExercise type
        rest: 60, // Default rest time in seconds
        notes: '',
        difficultyLevel: 'medium',
        equipmentRequired: '',
        targetIntensity: '5',
        primaryMuscleGroup: ex.muscleGroup,
        secondaryMuscleGroup: '',
        exerciseDuration: '',
        recoveryTime: '',
        exerciseType: 'strength',
        caloriesBurned: '',
        exerciseProgression: '',
        injuryRiskLevel: 'low',
        exerciseLink: '',
        imageLink: '',
        relativePath: ''
      })),
      createdAt: new Date().toISOString(),
      lastUsed: ''
    });
    
    // Then start the workout with the template name
    startWorkout(templateName);
    
    toast.success("Workout Started", {
      description: "Let's get training!"
    });
  };

  return (
    <Card>      <CardHeader>
        <CardTitle className="text-lg font-semibold flex items-center">
          <Lightbulb className="h-5 w-5 mr-2 text-primary" />
          Workout Recommender
        </CardTitle>
        <CardDescription>
          Generate personalized workout plans based on your muscle recovery status
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="grid gap-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <label className="text-sm font-medium">Workout Type</label>              <Select 
                value={workoutType} 
                onValueChange={(value: string) => setWorkoutType(value as 'fullBody' | 'split' | 'push' | 'pull' | 'legs')}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select workout type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="fullBody">Full Body</SelectItem>
                  <SelectItem value="push">Push (Chest/Shoulders/Triceps)</SelectItem>
                  <SelectItem value="pull">Pull (Back/Biceps)</SelectItem>
                  <SelectItem value="legs">Legs</SelectItem>
                  <SelectItem value="split">Muscle Split</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            <div className="space-y-2">
              <label className="text-sm font-medium">Duration</label>              <Select 
                value={duration} 
                onValueChange={(value: string) => setDuration(value as 'short' | 'medium' | 'long')}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select duration" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="short">Short (15-30 min)</SelectItem>
                  <SelectItem value="medium">Medium (30-45 min)</SelectItem>
                  <SelectItem value="long">Long (45-60+ min)</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
          
          <Button onClick={generateWorkout} disabled={isGenerating}>
            {isGenerating ? 'Generating...' : 'Generate Workout Plan'}
          </Button>
          
          {recommendedWorkout && (
            <div className="mt-4 space-y-4">
              <div>
                <h3 className="text-lg font-medium">{recommendedWorkout.name}</h3>
                <p className="text-sm text-muted-foreground">{recommendedWorkout.description}</p>
              </div>
              
              <div className="border rounded-md">
                <div className="p-3 border-b bg-muted/50">
                  <div className="grid grid-cols-12 text-sm font-medium">
                    <div className="col-span-5">Exercise</div>
                    <div className="col-span-3">Muscle Group</div>
                    <div className="col-span-2 text-center">Sets</div>
                    <div className="col-span-2 text-center">Reps</div>
                  </div>
                </div>
                
                <div className="divide-y">
                  {recommendedWorkout.exercises.map((exercise, idx) => (
                    <div key={idx} className="p-3">
                      <div className="grid grid-cols-12 text-sm">
                        <div className="col-span-5 font-medium">{exercise.name}</div>
                        <div className="col-span-3 text-muted-foreground">{exercise.muscleGroup}</div>
                        <div className="col-span-2 text-center">{exercise.sets}</div>
                        <div className="col-span-2 text-center">{exercise.reps}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>
      </CardContent>
      
      {recommendedWorkout && (
        <CardFooter className="flex justify-between pt-0">
          <Button 
            variant="outline" 
            onClick={handleSaveTemplate}
            className="flex items-center gap-2"
          >
            <Save className="h-4 w-4" />
            Save as Template
          </Button>
          
          <Button 
            onClick={handleStartWorkout}
            className="flex items-center gap-2"
          >
            <PlayCircle className="h-4 w-4" />
            Start Workout
          </Button>
        </CardFooter>
      )}
    </Card>
  );
};
