// src/utils/muscleFatigueCalculator.ts
import { WorkoutLogEntry } from '@/types/Workout_Log';
import type { FatigueSettings } from '@/contexts/FatigueSettingsContext';

export interface MuscleFatigueData {
  muscleGroup: string;
  fatigue: number;
  lastTrainingDate: string | null;
  recoveryStatus: 'Ready' | 'Recovering' | 'Fatigued';
  suggestedRestDays: number;
}

interface MuscleTrainingData {
  lastTrainingDate: string;
  recentVolume: number;
  recentIntensity: number;
}

// Extended interfaces for advanced analytics
export interface MuscleRecoveryProfile {
  muscleGroup: string;
  baseRecoveryRate: number;
  personalizedRecoveryRate: number;
  optimalTrainingFrequency: number;
  averageRecoveryDays: number;
  lastWorkouts: WorkoutLogEntry[];
}

export interface FatigueInsight {
  muscleGroup: string;
  insight: string;
  type: 'positive' | 'warning' | 'info';
  suggestion: string;
}

// Constants for fatigue calculation (will be replaced by user settings)
// These serve as defaults when no settings are provided
const DEFAULT_MAX_FATIGUE = 100;
const DEFAULT_RECOVERY_RATE = 15; // % recovery per day
const DEFAULT_FATIGUE_THRESHOLD = {
  low: 20,    // Ready to train (updated to match interface)
  medium: 60, // Recovering but can train (updated to match interface)
  high: 80    // Needs rest (updated to match interface)
};

/**
 * Calculates muscle fatigue and recovery status based on recent workout history
 * @param logs Array of workout logs
 * @param daysToConsider Days of workout history to consider for fatigue calculation (default: 7)
 * @param settings Optional fatigue settings to override defaults
 * @returns Array of muscle groups with their fatigue and recovery status
 */
export function calculateMuscleFatigue(
  logs: WorkoutLogEntry[],
  daysToConsider: number = 7,
  settings?: FatigueSettings
): MuscleFatigueData[] {
  if (!logs || logs.length === 0) return [];

  // Use provided settings or defaults
  const maxFatigue = settings?.maxFatigue ?? DEFAULT_MAX_FATIGUE;
  const recoveryRate = settings?.recoveryRate ?? DEFAULT_RECOVERY_RATE;
  const thresholds = settings?.thresholds ?? DEFAULT_FATIGUE_THRESHOLD;
  const daysToUse = settings?.daysToConsider ?? daysToConsider;

  // Get current date
  const currentDate = new Date();
  
  // Calculate cutoff date using configurable days
  const cutoffDate = new Date(currentDate);
  cutoffDate.setDate(currentDate.getDate() - daysToUse);
  
  // Filter logs to only include recent ones
  const recentLogs = logs.filter(log => {
    const logDate = new Date(log.date);
    return logDate >= cutoffDate;
  });
  
  // Find all unique muscle groups
  const muscleGroups = [...new Set(recentLogs.map(log => log.muscleGroup))];
  
  // Calculate the muscle group training data from logs
  const muscleTrainingData: Record<string, MuscleTrainingData> = {};
  
  recentLogs.forEach(log => {
    const group = log.muscleGroup;
    const logDate = new Date(log.date);
    const volume = (log.weight || 0) * (log.reps || 0);
    const intensity = log.rating || 5; // Default to medium intensity if not provided
    
    if (!muscleTrainingData[group] || new Date(muscleTrainingData[group].lastTrainingDate) < logDate) {
      // Initialize or update if this is a more recent training
      muscleTrainingData[group] = {
        lastTrainingDate: log.date,
        recentVolume: volume,
        recentIntensity: intensity
      };
    } else {
      // Accumulate volume and intensity if same date
      if (muscleTrainingData[group].lastTrainingDate === log.date) {
        muscleTrainingData[group].recentVolume += volume;
        muscleTrainingData[group].recentIntensity = Math.max(muscleTrainingData[group].recentIntensity, intensity);
      }
    }
  });
  
  // Calculate maximum volume achieved by any muscle group in the period
  const maxVolume = Math.max(
    1, // Prevent division by zero
    ...Object.values(muscleTrainingData).map(data => data.recentVolume)
  );
  
  // Calculate fatigue for each muscle group
  return muscleGroups.map(group => {
    const data = muscleTrainingData[group] || {
      lastTrainingDate: null,
      recentVolume: 0,
      recentIntensity: 0
    };
    
    let fatigue = (data.recentVolume / maxVolume) * 100; // Base fatigue on relative volume
    
    // Adjust fatigue based on intensity
    fatigue = fatigue * (data.recentIntensity / 5); // Scale by intensity
    
    // Apply recovery based on days since last training
    if (data.lastTrainingDate) {
      const daysSinceTraining = Math.floor(
        (currentDate.getTime() - new Date(data.lastTrainingDate).getTime()) / 
        (1000 * 60 * 60 * 24)
      );
      
      // Apply configurable recovery rate for each day
      const recovery = daysSinceTraining * recoveryRate;
      fatigue = Math.max(0, fatigue - recovery);
    }
    
    // Cap fatigue at configurable max fatigue
    fatigue = Math.min(maxFatigue, fatigue);
    
    // Determine recovery status using configurable thresholds
    let recoveryStatus: 'Ready' | 'Recovering' | 'Fatigued'; 
    if (fatigue < thresholds.low) {
      recoveryStatus = 'Ready';
    } else if (fatigue < thresholds.high) {
      recoveryStatus = 'Recovering';
    } else {
      recoveryStatus = 'Fatigued';
    }
    
    // Calculate suggested rest days using configurable recovery rate
    const suggestedRestDays = Math.ceil(fatigue / recoveryRate);
    
    return {
      muscleGroup: group,
      fatigue: Math.round(fatigue),
      lastTrainingDate: data.lastTrainingDate,
      recoveryStatus,
      suggestedRestDays
    };
  });
}

/**
 * Returns muscle groups that are ready for training
 * @param fatigueData Array of muscle fatigue data
 * @returns Array of muscle groups ready for training
 */
export function getMusclesReadyForTraining(fatigueData: MuscleFatigueData[]): string[] {
  return fatigueData
    .filter(data => data.recoveryStatus === 'Ready')
    .map(data => data.muscleGroup);
}

/**
 * Returns muscle groups that need rest
 * @param fatigueData Array of muscle fatigue data
 * @returns Array of muscle groups that need rest
 */
export function getMusclesNeedingRest(fatigueData: MuscleFatigueData[]): string[] {
  return fatigueData
    .filter(data => data.recoveryStatus === 'Fatigued')
    .map(data => data.muscleGroup);
}

/**
 * Calculates personalized recovery profiles for each muscle group
 * @param logs Complete workout history
 * @param settings Optional fatigue settings to override defaults
 * @returns Recovery profiles for each trained muscle group
 */
export function calculateRecoveryProfiles(
  logs: WorkoutLogEntry[],
  settings?: FatigueSettings
): MuscleRecoveryProfile[] {
  if (!logs || logs.length === 0) return [];

  // Use provided settings or defaults
  const recoveryRate = settings?.recoveryRate ?? DEFAULT_RECOVERY_RATE;

  // Group logs by muscle group
  const muscleGroupLogs: Record<string, WorkoutLogEntry[]> = {};
  logs.forEach(log => {
    if (!muscleGroupLogs[log.muscleGroup]) {
      muscleGroupLogs[log.muscleGroup] = [];
    }
    muscleGroupLogs[log.muscleGroup].push(log);
  });

  // Calculate recovery profiles for each muscle group
  return Object.entries(muscleGroupLogs).map(([muscleGroup, muscleGroupHistory]) => {
    // Sort workout logs by date (newest first)
    const sortedLogs = [...muscleGroupHistory].sort((a, b) => 
      new Date(b.date).getTime() - new Date(a.date).getTime()
    );
    
    // Calculate average recovery days (time between workouts)
    let totalDays = 0;
    let countIntervals = 0;
    
    for (let i = 0; i < sortedLogs.length - 1; i++) {
      const currDate = new Date(sortedLogs[i].date);
      const nextDate = new Date(sortedLogs[i+1].date);
      
      // Calculate days between workouts
      const days = Math.abs(Math.round((currDate.getTime() - nextDate.getTime()) / (1000 * 60 * 60 * 24)));
      
      // Only count reasonable intervals (not months apart)
      if (days > 0 && days < 14) {
        totalDays += days;
        countIntervals++;
      }
    }
    
    const averageRecoveryDays = countIntervals > 0 ? totalDays / countIntervals : 0;
    
    // Calculate personalized recovery rate
    // Base this on how quickly this person typically recovers from workouts
    const personalizedRecoveryRate = averageRecoveryDays > 0 
      ? Math.min(25, Math.max(10, 100 / averageRecoveryDays)) // Range: 10-25%
      : recoveryRate; // Default if no data
    
    // Determine optimal training frequency
    // This is the recommended days between workouts for this muscle group
    const optimalTrainingFrequency = Math.max(1, Math.min(7, Math.round(100 / personalizedRecoveryRate)));
    
    return {
      muscleGroup,
      baseRecoveryRate: recoveryRate,
      personalizedRecoveryRate: Math.round(personalizedRecoveryRate * 10) / 10,
      optimalTrainingFrequency: optimalTrainingFrequency,
      averageRecoveryDays: Math.round(averageRecoveryDays * 10) / 10,
      lastWorkouts: sortedLogs.slice(0, 3) // Include last 3 workouts
    };
  });
}

/**
 * Generates insights about muscle fatigue and recovery patterns
 * @param fatigueData Current fatigue data
 * @param recoveryProfiles Recovery profiles
 * @returns Array of insights with recommendations
 */
export function generateFatigueInsights(
  fatigueData: MuscleFatigueData[],
  recoveryProfiles: MuscleRecoveryProfile[]
): FatigueInsight[] {
  const insights: FatigueInsight[] = [];
  
  // Match fatigue data with recovery profiles
  fatigueData.forEach(data => {
    const profile = recoveryProfiles.find(p => p.muscleGroup === data.muscleGroup);
    if (!profile) return;
    
    // Generate insights based on fatigue level and recovery patterns
    if (data.fatigue > 80) {
      insights.push({
        muscleGroup: data.muscleGroup,
        insight: `Your ${data.muscleGroup} is highly fatigued and needs proper rest.`,
        type: 'warning',
        suggestion: `Rest for at least ${data.suggestedRestDays} days before training again.`
      });
    } else if (data.fatigue < 10 && profile.averageRecoveryDays > 5) {
      insights.push({
        muscleGroup: data.muscleGroup,
        insight: `Your ${data.muscleGroup} is undertraining based on your recovery capacity.`,
        type: 'info',
        suggestion: `You could train this muscle group more frequently, approximately every ${profile.optimalTrainingFrequency} days.`
      });
    } else if (data.fatigue > 60 && profile.averageRecoveryDays < 2) {
      insights.push({
        muscleGroup: data.muscleGroup,
        insight: `You appear to be training ${data.muscleGroup} too frequently.`,
        type: 'warning',
        suggestion: `Consider increasing rest between workouts to at least ${profile.optimalTrainingFrequency} days.`
      });
    } else if (data.recoveryStatus === 'Ready') {
      insights.push({
        muscleGroup: data.muscleGroup,
        insight: `Your ${data.muscleGroup} has fully recovered and is ready for an effective workout.`,
        type: 'positive',
        suggestion: `For optimal growth, train with progressive overload and then allow ${profile.optimalTrainingFrequency} days for recovery.`
      });
    }
  });
  
  return insights;
}
