// src/views/MuscleFatiguePage.tsx
import React, { useMemo } from 'react';
import { ArrowLeft, Calendar, Activity, Check, AlertTriangle, Clock } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { calculateMuscleFatigue, MuscleFatigueData } from '@/utils/muscleFatigueCalculator';
import { Separator } from '@/components/ui/separator';
import { useWorkoutLogs, useExercises, useConsolidatedApp } from '@/hooks/useConsolidatedApp';
import { WorkoutRecommender } from '@/components/workout/WorkoutRecommender';
import { MuscleFatigueAnalytics } from '@/components/workout/MuscleFatigueAnalytics';
import { AllFatigueInsights } from '@/components/workout/AllFatigueInsights';
import { useFatigueSettings } from '@/contexts/FatigueSettingsContext';

export default function MuscleFatiguePage() {
  const { appStore } = useConsolidatedApp();
  const { setCurrentView } = appStore;
  const { workoutLogs } = useWorkoutLogs();
  const { getExercisesByMuscleGroup } = useExercises();
  const { settings } = useFatigueSettings();
  
  // Calculate muscle fatigue data
  const fatigueData = useMemo(() => {
    return calculateMuscleFatigue(workoutLogs, undefined, settings);
  }, [workoutLogs, settings]);

  // Group muscles by recovery status
  const groupedMuscles = useMemo(() => {
    const grouped = {
      ready: fatigueData.filter(data => data.recoveryStatus === 'Ready'),
      recovering: fatigueData.filter(data => data.recoveryStatus === 'Recovering'),
      fatigued: fatigueData.filter(data => data.recoveryStatus === 'Fatigued')
    };
    
    // Sort each group by fatigue level
    return {
      ready: grouped.ready.sort((a, b) => a.fatigue - b.fatigue),
      recovering: grouped.recovering.sort((a, b) => a.fatigue - b.fatigue),
      fatigued: grouped.fatigued.sort((a, b) => b.fatigue - a.fatigue)
    };
  }, [fatigueData]);
  // Check if there's no workout log data
  if (!workoutLogs || workoutLogs.length === 0) {
    return (
      <div className="container max-w-3xl mx-auto py-4 px-4">
        <div className="flex items-center mb-4">
          <Button variant="outline" size="icon" onClick={() => setCurrentView('dashboard')} className="mr-2">
            <ArrowLeft className="h-4 w-4" />
          </Button>
          <h1 className="text-2xl font-bold">Muscle Fatigue Tracker</h1>
        </div>
        
        <Card>
          <CardContent className="flex flex-col items-center justify-center h-60 text-center">
            <Activity className="h-16 w-16 text-muted-foreground mb-4" />
            <p className="text-xl font-medium mb-2">No workout data available</p>
            <p className="text-muted-foreground mb-4">Log your workouts to track muscle fatigue and recovery status</p>
            <Button onClick={() => setCurrentView('workouts')}>
              Log a Workout
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }
  return (
    <div className="container max-w-3xl mx-auto py-4 px-4">
      <div className="flex items-center mb-4">
        <Button variant="outline" size="icon" onClick={() => setCurrentView('dashboard')} className="mr-2">
          <ArrowLeft className="h-4 w-4" />
        </Button>
        <h1 className="text-2xl font-bold">Muscle Fatigue Tracker</h1>
      </div>

      <div className="grid gap-4 mb-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle>Recovery Status</CardTitle>
            <CardDescription>Train muscles that are ready, avoid those that need rest</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-3 gap-2 text-center">
              <div className="p-2 bg-green-500/10 rounded-md">
                <Check className="h-5 w-5 text-green-500 mx-auto mb-1" />
                <p className="font-medium">{groupedMuscles.ready.length}</p>
                <p className="text-xs text-muted-foreground">Ready to Train</p>
              </div>
              <div className="p-2 bg-yellow-500/10 rounded-md">
                <Clock className="h-5 w-5 text-yellow-500 mx-auto mb-1" />
                <p className="font-medium">{groupedMuscles.recovering.length}</p>
                <p className="text-xs text-muted-foreground">Recovering</p>
              </div>
              <div className="p-2 bg-red-500/10 rounded-md">
                <AlertTriangle className="h-5 w-5 text-red-500 mx-auto mb-1" />
                <p className="font-medium">{groupedMuscles.fatigued.length}</p>
                <p className="text-xs text-muted-foreground">Needs Rest</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Tabs defaultValue="ready">
          <TabsList className="grid grid-cols-3 mb-2">
            <TabsTrigger value="ready">Ready to Train</TabsTrigger>
            <TabsTrigger value="recovering">Recovering</TabsTrigger>
            <TabsTrigger value="fatigued">Needs Rest</TabsTrigger>
          </TabsList>
          
          <TabsContent value="ready">
            <Card>
              <CardContent className="pt-6">
                {groupedMuscles.ready.length === 0 ? (
                  <p className="text-center py-8 text-muted-foreground">No muscle groups are ready for training</p>
                ) : (
                  <div className="grid gap-4">
                    {groupedMuscles.ready.map(muscle => (
                      <MuscleDetails 
                        key={muscle.muscleGroup} 
                        muscle={muscle} 
                        getExercises={getExercisesByMuscleGroup}
                      />
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
          
          <TabsContent value="recovering">
            <Card>
              <CardContent className="pt-6">
                {groupedMuscles.recovering.length === 0 ? (
                  <p className="text-center py-8 text-muted-foreground">No muscle groups are recovering</p>
                ) : (
                  <div className="grid gap-4">
                    {groupedMuscles.recovering.map(muscle => (
                      <MuscleDetails 
                        key={muscle.muscleGroup} 
                        muscle={muscle} 
                        getExercises={getExercisesByMuscleGroup}
                      />
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
          
          <TabsContent value="fatigued">
            <Card>
              <CardContent className="pt-6">
                {groupedMuscles.fatigued.length === 0 ? (
                  <p className="text-center py-8 text-muted-foreground">No muscle groups need rest</p>
                ) : (
                  <div className="grid gap-4">
                    {groupedMuscles.fatigued.map(muscle => (
                      <MuscleDetails 
                        key={muscle.muscleGroup} 
                        muscle={muscle} 
                        getExercises={getExercisesByMuscleGroup}
                      />
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>          </TabsContent>
        </Tabs>
      </div>
        {/* Add the WorkoutRecommender component */}
      <div className="mt-6 grid gap-6">
        <WorkoutRecommender logs={workoutLogs} />
        
        <AllFatigueInsights logs={workoutLogs} />
        
        {/* Add the MuscleFatigueAnalytics component */}
        <MuscleFatigueAnalytics logs={workoutLogs} />
      </div>
    </div>
  );
}

// Component for muscle details
interface MuscleDetailsProps {
  muscle: MuscleFatigueData;
  getExercises: (muscleGroup: string) => string[];
}

const MuscleDetails: React.FC<MuscleDetailsProps> = ({ muscle, getExercises }) => {
  // Convert the string array to the required format
  const exercises = getExercises(muscle.muscleGroup)
    .slice(0, 5) // Show top 5 exercises
    .map(exerciseName => ({ 
      exercise: exerciseName, 
      muscleGroup: muscle.muscleGroup 
    }));

  const getFatigueColor = (fatigue: number) => {
    if (fatigue < 20) return 'bg-green-500';
    if (fatigue < 60) return 'bg-yellow-500';
    return 'bg-red-500';
  };

  const getStatusColor = (status: string) => {
    if (status === 'Ready') return 'text-green-500';
    if (status === 'Recovering') return 'text-yellow-500';
    return 'text-red-500';
  };

  const getStatusIcon = (status: string) => {
    if (status === 'Ready') return <Check className="h-4 w-4 text-green-500" />;
    if (status === 'Recovering') return <Clock className="h-4 w-4 text-yellow-500" />;
    return <AlertTriangle className="h-4 w-4 text-red-500" />;
  };

  return (
    <div className="border rounded-lg overflow-hidden">
      <div className="p-4">
        <div className="flex items-center justify-between mb-2">
          <h3 className="text-lg font-medium">{muscle.muscleGroup}</h3>
          <div className={`flex items-center ${getStatusColor(muscle.recoveryStatus)}`}>
            {getStatusIcon(muscle.recoveryStatus)}
            <span className="ml-1 text-sm">{muscle.recoveryStatus}</span>
          </div>
        </div>
        
        <div className="mb-3">
          <div className="flex justify-between text-sm mb-1">
            <span>Fatigue Level</span>
            <span className="font-medium">{muscle.fatigue}%</span>
          </div>
          <Progress value={muscle.fatigue} className={`h-2 ${getFatigueColor(muscle.fatigue)}`} />
        </div>
        
        <div className="grid grid-cols-2 gap-2 text-sm mb-4">
          <div className="flex items-center">
            <Calendar className="h-4 w-4 mr-2 text-muted-foreground" />
            <span>Last Training: </span>
            <span className="ml-1 font-medium">
              {muscle.lastTrainingDate ? new Date(muscle.lastTrainingDate).toLocaleDateString() : 'Never'}
            </span>
          </div>
          <div className="flex items-center">
            <Clock className="h-4 w-4 mr-2 text-muted-foreground" />
            <span>Rest Needed: </span>
            <span className="ml-1 font-medium">
              {muscle.suggestedRestDays > 0 ? `${muscle.suggestedRestDays} days` : 'None'}
            </span>
          </div>
        </div>
        
        {exercises.length > 0 && (
          <>
            <Separator className="my-2" />
            <div className="mt-3">
              <h4 className="text-sm font-medium mb-2">Suggested Exercises:</h4>
              <div className="grid grid-cols-1 gap-1">
                {exercises.map((exercise, index) => (
                  <div key={index} className="text-sm px-2 py-1 bg-muted/40 rounded">
                    {exercise.exercise}
                  </div>
                ))}
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};
