// src/components/workout/MuscleFatigueAnalytics.tsx
import React, { useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { calculateMuscleFatigue } from '@/utils/muscleFatigueCalculator';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer, CartesianGrid, Legend } from 'recharts';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { Separator } from '@/components/ui/separator';
import { TrendingUp, Calendar, Info } from 'lucide-react';
import { useFatigueSettings } from '@/contexts/FatigueSettingsContext';

interface MuscleFatigueAnalyticsProps {
  logs: WorkoutLogEntry[];
}

export const MuscleFatigueAnalytics: React.FC<MuscleFatigueAnalyticsProps> = ({ logs }) => {
  const { settings } = useFatigueSettings();
  
  // Calculate current muscle fatigue levels
  const currentFatigue = useMemo(() => {
    return calculateMuscleFatigue(logs, undefined, settings);
  }, [logs, settings]);
  
  // Group logs by date for time-based analysis
  const logsByDate = useMemo(() => {
    const grouped = new Map<string, WorkoutLogEntry[]>();
    
    // Sort logs by date (newest first)
    const sortedLogs = [...logs].sort((a, b) => 
      new Date(b.date).getTime() - new Date(a.date).getTime()
    );
    
    // Group logs by date
    sortedLogs.forEach(log => {
      const date = new Date(log.date).toLocaleDateString();
      if (!grouped.has(date)) {
        grouped.set(date, []);
      }
      grouped.get(date)?.push(log);
    });
    
    return grouped;
  }, [logs]);
  
  // Calculate muscle fatigue over time (last 14 days)
  const fatigueOverTime = useMemo(() => {
    const result = [];
    const today = new Date();
    
    // Create data for the last 14 days
    for (let i = 13; i >= 0; i--) {
      const date = new Date(today);
      date.setDate(date.getDate() - i);
      const dateStr = date.toLocaleDateString();
      
      // Filter logs before or on this date
      const logsBeforeDate = logs.filter(log => 
        new Date(log.date) <= date
      );
        // Calculate fatigue for this date
      const fatigue = calculateMuscleFatigue(logsBeforeDate, undefined, settings);
      
      // Average fatigue by muscle group
      const muscleGroups = new Map<string, number>();
      fatigue.forEach(f => {
        muscleGroups.set(f.muscleGroup, f.fatigue);
      });
      
      // Create data point
      result.push({
        date: dateStr,
        ...Object.fromEntries(muscleGroups.entries())
      });
    }
      return result;
  }, [logs, settings]);
  
  // Get primary muscle groups
  const primaryMuscleGroups = useMemo(() => {
    const groups = new Set<string>();
    currentFatigue.forEach(f => groups.add(f.muscleGroup));
    return Array.from(groups);
  }, [currentFatigue]);
  
  // Get training frequency by muscle group
  const trainingFrequency = useMemo(() => {
    const frequency: Record<string, number> = {};
    
    logs.forEach(log => {
      const muscleGroup = log.muscleGroup;
      if (!frequency[muscleGroup]) {
        frequency[muscleGroup] = 0;
      }
      frequency[muscleGroup]++;
    });
    
    return Object.entries(frequency)
      .map(([muscleGroup, count]) => ({ muscleGroup, count }))
      .sort((a, b) => b.count - a.count);
  }, [logs]);
  
  // Calculate optimal training frequency by muscle group
  const optimalTrainingFrequency = useMemo(() => {
    return primaryMuscleGroups.map(muscleGroup => {
      // Get fatigue level for this muscle group
      const fatigue = currentFatigue.find(f => f.muscleGroup === muscleGroup)?.fatigue || 0;
      
      // Calculate optimal training frequency (days between workouts)
      // This is an estimation based on fatigue level:
      // - Low fatigue muscles (0-20): can train every 1-2 days
      // - Medium fatigue muscles (20-60): train every 2-3 days
      // - High fatigue muscles (60-100): train every 3-5 days
      let daysPerWorkout = 1;
      if (fatigue < 20) {
        daysPerWorkout = 1;
      } else if (fatigue < 60) {
        daysPerWorkout = 2;
      } else {
        daysPerWorkout = 3;
      }
      
      return {
        muscleGroup,
        daysPerWorkout,
        workoutsPerWeek: Math.round(7 / daysPerWorkout),
        currentFatigue: fatigue
      };
    });
  }, [primaryMuscleGroups, currentFatigue]);
  
  // Calculate recovery patterns
  const recoveryPatterns = useMemo(() => {
    const patterns: Record<string, { avgRecoveryDays: number; lastTrainingDate: string | null }> = {};
    
    // Go through each unique muscle group
    primaryMuscleGroups.forEach(muscleGroup => {
      // Filter logs for this muscle group
      const muscleGroupLogs = logs.filter(log => log.muscleGroup === muscleGroup);
      
      // Sort logs by date (oldest first)
      const sortedLogs = [...muscleGroupLogs].sort((a, b) => 
        new Date(a.date).getTime() - new Date(b.date).getTime()
      );
      
      // Calculate days between workouts
      let totalDays = 0;
      let countIntervals = 0;
      
      for (let i = 1; i < sortedLogs.length; i++) {
        const prevDate = new Date(sortedLogs[i-1].date);
        const currDate = new Date(sortedLogs[i].date);
        
        // Calculate days between workouts
        const days = Math.round((currDate.getTime() - prevDate.getTime()) / (1000 * 60 * 60 * 24));
        
        // Only count if it's a reasonable interval (not months)
        if (days > 0 && days < 14) {
          totalDays += days;
          countIntervals++;
        }
      }
      
      patterns[muscleGroup] = {
        avgRecoveryDays: countIntervals > 0 ? Math.round(totalDays / countIntervals * 10) / 10 : 0,
        lastTrainingDate: sortedLogs.length > 0 ? sortedLogs[sortedLogs.length - 1].date : null
      };
    });
    
    return patterns;
  }, [logs, primaryMuscleGroups]);
  
  if (!logs || logs.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="text-lg font-semibold flex items-center">
            <TrendingUp className="h-5 w-5 mr-2 text-primary" />
            Muscle Fatigue Analytics
          </CardTitle>
          <CardDescription>
            Log workouts to see analytics and insights about your muscle recovery patterns
          </CardDescription>
        </CardHeader>
        <CardContent className="flex items-center justify-center h-60">
          <p className="text-muted-foreground">No workout data available</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg font-semibold flex items-center">
          <TrendingUp className="h-5 w-5 mr-2 text-primary" />
          Muscle Fatigue Analytics
        </CardTitle>
        <CardDescription>
          Insights and patterns about your muscle recovery and training frequency
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="overview">
          <TabsList className="grid grid-cols-3 mb-4">
            <TabsTrigger value="overview">Overview</TabsTrigger>
            <TabsTrigger value="frequency">Training Frequency</TabsTrigger>
            <TabsTrigger value="recovery">Recovery Patterns</TabsTrigger>
          </TabsList>
          
          <TabsContent value="overview">
            <div className="space-y-4">
              <h3 className="text-sm font-medium">Current Muscle Fatigue Levels</h3>
              
              <div className="h-64 w-full">
                <ResponsiveContainer width="100%" height="100%">
                  <BarChart
                    data={currentFatigue.sort((a, b) => b.fatigue - a.fatigue)}
                    margin={{ top: 20, right: 30, left: 20, bottom: 5 }}
                  >
                    <CartesianGrid strokeDasharray="3 3" opacity={0.2} />
                    <XAxis 
                      dataKey="muscleGroup" 
                      fontSize={12} 
                      angle={-45} 
                      textAnchor="end" 
                      height={60} 
                    />
                    <YAxis 
                      label={{ value: 'Fatigue %', angle: -90, position: 'insideLeft' }} 
                      domain={[0, 100]} 
                    />
                    <Tooltip />
                    <Bar 
                      dataKey="fatigue" 
                      name="Fatigue Level" 
                      fill="var(--primary)" 
                      radius={[4, 4, 0, 0]} 
                    />
                  </BarChart>
                </ResponsiveContainer>
              </div>
              
              <Separator className="my-4" />
              
              <div>
                <h3 className="text-sm font-medium mb-2">Recent Workout Activity</h3>
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                  {Array.from(logsByDate.entries()).slice(0, 4).map(([date, dayLogs]) => (
                    <div key={date} className="p-3 bg-muted/30 rounded-md">
                      <div className="flex items-center mb-2">
                        <Calendar className="h-4 w-4 mr-2 text-muted-foreground" />
                        <span className="font-medium">{date}</span>
                      </div>
                      <div className="grid gap-1">
                        {Array.from(new Set(dayLogs.map(log => log.muscleGroup))).map((muscle, idx) => (
                          <div key={idx} className="text-xs flex justify-between">
                            <span>{muscle}</span>
                            <span className="text-muted-foreground">
                              {dayLogs.filter(log => log.muscleGroup === muscle).length} exercises
                            </span>
                          </div>
                        ))}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </TabsContent>
          
          <TabsContent value="frequency">
            <div className="space-y-4">
              <div className="h-64 w-full">
                <ResponsiveContainer width="100%" height="100%">
                  <BarChart
                    data={trainingFrequency}
                    margin={{ top: 20, right: 30, left: 20, bottom: 5 }}
                  >
                    <CartesianGrid strokeDasharray="3 3" opacity={0.2} />
                    <XAxis 
                      dataKey="muscleGroup" 
                      fontSize={12} 
                      angle={-45} 
                      textAnchor="end" 
                      height={60} 
                    />
                    <YAxis 
                      label={{ value: 'Workout Count', angle: -90, position: 'insideLeft' }} 
                    />
                    <Tooltip />
                    <Bar 
                      dataKey="count" 
                      name="Training Sessions" 
                      fill="var(--primary)" 
                      radius={[4, 4, 0, 0]} 
                    />
                  </BarChart>
                </ResponsiveContainer>
              </div>
              
              <Separator className="my-4" />
              
              <div>
                <h3 className="text-sm font-medium mb-2 flex items-center gap-1">
                  Optimal Training Frequency
                  <Info className="h-4 w-4 text-muted-foreground" />
                </h3>
                <p className="text-xs text-muted-foreground mb-3">
                  Based on your recovery patterns and current fatigue levels
                </p>
                
                <div className="overflow-x-auto">
                  <table className="w-full border-collapse">
                    <thead>
                      <tr className="border-b">
                        <th className="text-left p-2 text-xs font-medium">Muscle Group</th>
                        <th className="text-center p-2 text-xs font-medium">Current Fatigue</th>
                        <th className="text-center p-2 text-xs font-medium">Optimal Days Between</th>
                        <th className="text-center p-2 text-xs font-medium">Workouts Per Week</th>
                      </tr>
                    </thead>
                    <tbody>
                      {optimalTrainingFrequency.map((item, idx) => (
                        <tr key={idx} className={idx % 2 === 0 ? 'bg-muted/20' : ''}>
                          <td className="p-2 text-xs">{item.muscleGroup}</td>
                          <td className="p-2 text-xs text-center">{item.currentFatigue}%</td>
                          <td className="p-2 text-xs text-center">{item.daysPerWorkout}</td>
                          <td className="p-2 text-xs text-center">{item.workoutsPerWeek}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </TabsContent>
          
          <TabsContent value="recovery">
            <div className="space-y-4">
              <h3 className="text-sm font-medium">Muscle Recovery Patterns</h3>
              
              <div className="overflow-x-auto">
                <table className="w-full border-collapse">
                  <thead>
                    <tr className="border-b">
                      <th className="text-left p-2 text-xs font-medium">Muscle Group</th>
                      <th className="text-center p-2 text-xs font-medium">Avg. Days Between Workouts</th>
                      <th className="text-center p-2 text-xs font-medium">Last Trained</th>
                      <th className="text-center p-2 text-xs font-medium">Current Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {primaryMuscleGroups.map((muscle, idx) => {
                      const pattern = recoveryPatterns[muscle];
                      const fatigue = currentFatigue.find(f => f.muscleGroup === muscle);
                      
                      return (
                        <tr key={idx} className={idx % 2 === 0 ? 'bg-muted/20' : ''}>
                          <td className="p-2 text-xs">{muscle}</td>
                          <td className="p-2 text-xs text-center">
                            {pattern?.avgRecoveryDays || 'N/A'}
                          </td>
                          <td className="p-2 text-xs text-center">
                            {pattern?.lastTrainingDate 
                              ? new Date(pattern.lastTrainingDate).toLocaleDateString() 
                              : 'Never'}
                          </td>
                          <td className="p-2 text-xs text-center">
                            <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium
                              ${fatigue?.recoveryStatus === 'Ready' 
                                ? 'bg-green-500/10 text-green-500' 
                                : fatigue?.recoveryStatus === 'Recovering' 
                                  ? 'bg-yellow-500/10 text-yellow-500' 
                                  : 'bg-red-500/10 text-red-500'}`}>
                              {fatigue?.recoveryStatus || 'Unknown'}
                            </span>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
              
              <Separator className="my-4" />
              
              <div>
                <h3 className="text-sm font-medium mb-3">Recovery Insights</h3>
                <div className="grid gap-2">
                  {primaryMuscleGroups.map((muscle, idx) => {
                    const fatigue = currentFatigue.find(f => f.muscleGroup === muscle);
                    const pattern = recoveryPatterns[muscle];
                    
                    // Skip if we don't have enough data
                    if (!fatigue || !pattern || pattern.avgRecoveryDays === 0) return null;
                    
                    let insight = '';
                    let insightType: 'positive' | 'warning' | 'info' = 'info';
                    
                    if (fatigue.fatigue > 70) {
                      insight = `Your ${muscle} is highly fatigued. Consider resting for at least ${fatigue.suggestedRestDays} more days.`;
                      insightType = 'warning';
                    } else if (pattern.avgRecoveryDays < 2 && fatigue.fatigue > 40) {
                      insight = `You may be training ${muscle} too frequently. Consider increasing rest between workouts.`;
                      insightType = 'warning';
                    } else if (pattern.avgRecoveryDays > 5 && fatigue.fatigue < 20) {
                      insight = `You could train ${muscle} more frequently to optimize gains.`;
                      insightType = 'info';
                    } else if (fatigue.recoveryStatus === 'Ready') {
                      insight = `Your ${muscle} has recovered well and is ready for an effective training session.`;
                      insightType = 'positive';
                    }
                    
                    if (!insight) return null;
                    
                    return (
                      <div 
                        key={idx} 
                        className={`p-3 rounded-md text-xs
                          ${insightType === 'positive' ? 'bg-green-500/10' : 
                            insightType === 'warning' ? 'bg-red-500/10' : 
                            'bg-blue-500/10'}`}
                      >
                        {insight}
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};
